import React, { useState, useEffect } from 'react';
import axios from '../../utils/axios';
import requests from '../../utils/requests';
import movieTrailer from 'movie-trailer';
import MovieDetail from '../../components/browse/MovieDetail';
import './SearchResult.css';

const base_url = 'https://image.tmdb.org/t/p/original';

const SearchResult = ({ query }) => {
  // States to manage movies, selected movie, and trailer URL
  const [movies, setMovies] = useState([]);
  const [selectedMovie, setSelectedMovie] = useState(null);
  const [trailerUrl, setTrailerUrl] = useState('');

  // URL for fetching search results based on the query
  const url = `${requests.fetchSearch}&query=${query}`;

  useEffect(() => {
    async function fetchData() {
      if (query) {
        const request = await axios.get(url);
        setMovies(request.data.results);
      } else {
        setMovies([]); // Reset movies if query is empty
      }
    }

    fetchData(); // Fetch data based on query when query changes
  }, [url, query]);

  // Function to handle click on a movie poster
  const handleClick = (movie) => {
    if (selectedMovie && selectedMovie.id === movie.id) {
      setSelectedMovie(null);
      setTrailerUrl('');
    } else {
      setSelectedMovie(movie);
      movieTrailer(movie?.title || '')
        .then((url) => {
          const urlParams = new URLSearchParams(new URL(url).search);
          setTrailerUrl(urlParams.get('v')); // Extract video ID for the trailer
        })
        .catch((error) => console.log(error));
    }
  };

  return (
    <div className='row'>
      {/* Displaying the search result header */}
      <h2>Search Result</h2>
      <div style={{ padding: '40px' }}>
        {/* Display MovieDetail component if a movie is selected */}
        {selectedMovie && <MovieDetail movieData={selectedMovie} movieTrailer={trailerUrl} />}
      </div>
      <div className='row_posters search-resul-container sc2'>
        {/* Display movie posters from search results */}
        {movies.map((movie) => {
          return (
            <img
              key={movie.id}
              onClick={() => handleClick(movie)} // Handle click on each poster
              className={`row_poster row_posterLarge`}
              src={`${base_url}${movie.poster_path}`}
              alt={movie.name}
            />
          );
        })}
      </div>
    </div>
  );
};

export default SearchResult;
