import React from 'react';
import YouTube from 'react-youtube';

import './MovieDetail.css';

// YouTube player options
const opts = {
  height: '400',
  width: '100%',
  playerVars: {
    autoplay: 0,
  },
};

const MovieDetail = ({ movieTrailer, movieData }) => {
  // Destructuring movieData object
  const { release_date, title, name, overview, vote_average } = movieData;

  return (
    <div className='movie_detail'>
      <div className='movie_detail_data'>
        {/* Display movie title */}
        <h1>{title || name}</h1>
        <hr />

        {/* Display release date and vote average */}
        <h3>Release Date: {release_date}</h3>
        <h3>Vote: {vote_average} / 10</h3>
        <br />
        {/* Display movie overview */}
        <p>{overview}</p>
      </div>
      <div className='movie_detail_trailer'>
        {/* Display YouTube video using react-youtube */}
        <YouTube
          videoId={movieTrailer} // Video ID for the trailer
          opts={opts} // YouTube player options
        />
      </div>
    </div>
  );
};

export default MovieDetail;
