import React, { useState, useEffect } from 'react';
import axios from '../../utils/axios';
import movieTrailer from 'movie-trailer'; // Importing movie-trailer package for fetching movie trailers
import MovieDetail from '../../components/browse/MovieDetail'; // Importing MovieDetail component
import './MovieList.css';

const base_url = 'https://image.tmdb.org/t/p/original'; // Base URL for movie images
const movies_limit = 10; // Limiting the number of displayed movies

function MovieList({ title, fetchUrl, isLargeRow }) {
  // States to manage movies, trailer URL, and selected movie
  const [movies, setMovies] = useState([]);
  const [trailerUrl, setTrailerUrl] = useState('');
  const [selectedMovie, setSelectedMovie] = useState(null);

  // Fetch movies from the given URL when the component mounts or 'fetchUrl' changes
  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(fetchUrl);
      setMovies(request.data.results);
      return request;
    }
    fetchData();
  }, [fetchUrl]);

  // Function to handle click on a movie poster
  const handleClick = (movie) => {
    if (selectedMovie && selectedMovie.id === movie.id) {
      // Deselect the movie if it's already selected
      setSelectedMovie(null);
      setTrailerUrl('');
    } else {
      // Select the clicked movie and fetch its trailer
      setSelectedMovie(movie);
      movieTrailer(movie?.title || '')
        .then((url) => {
          const urlParams = new URLSearchParams(new URL(url).search);
          setTrailerUrl(urlParams.get('v')); // Extract the video ID for the trailer
        })
        .catch((error) => console.log(error));
    }
  };

  // Limit the number of displayed movies and sort them by popularity
  movies.sort((a, b) => b.popularity - a.popularity);
  movies.splice(movies_limit);

  return (
    <div className='row'>
      {/* Display the title of the movie list */}
      <h2 className="movie-list-title">{title}</h2>
      <div className='row_posters sc2'>
        {/* Display movie posters */}
        {movies.map((movie) => {
          return (
            <img
              key={movie.id}
              onClick={() => handleClick(movie)} // Handle click on each poster
              className={`row_poster ${isLargeRow && 'row_posterLarge'}`}
              src={`${base_url}${
                isLargeRow ? movie.poster_path : movie.backdrop_path
              }`}
              alt={movie.name}
            />
          );
        })}
      </div>
      <div style={{ padding: '40px' }}>
        {/* Display MovieDetail component if a movie is selected */}
        {selectedMovie && <MovieDetail movieData={selectedMovie} movieTrailer={trailerUrl} />}
      </div>
    </div>
  );
}

export default MovieList;
